import {Link} from 'react-router-dom';

const Navbar = () => {
    return (
        <div className="navbar-comp">
            <div className="row">
                <div className="col-sm-8">
                    <ul>
                        <li><Link to="/"><strong>rocknrold</strong></Link></li>
                    </ul>
                </div>
                <div className="col-sm-4">
                    <ul>
                        <li><Link to="/projects"><strong><i>projects</i></strong></Link></li>
                        <li><Link to="/aboutme"><strong><i>about</i></strong></Link></li>
                        <li><Link to="/contact"><strong><i>contact</i></strong></Link></li>
                    </ul>
                </div>
            </div>
        </div>
    );
}
 
export default Navbar;