const Home = () => {
    return (
        <div className="home">
            <div className="home-content bg-light">
                <div className="row d-flex justify-content-center">
                    <div className="col col-md-7 text-justify">
                        <h1>Hello, I'm Aaron.</h1>
                        <p><i>A Bachelor of Science in <strong>Information Technology</strong> Undergraduate student from 
                            <strong> Technological University of the Philippines - Taguig</strong>, passoinate in 
                            web application development that utilizes different technologies and frameworks.</i></p>
                    </div>
                </div>
            </div>
            <div className="split pic-content bg-light">
                <div className="row d-flex justify-content-center">
                    <div className="col col-md-7 text-center">
                    <img className="rounded-circle" 
                        alt="100x100"
                        width="200px"
                        src={process.env.PUBLIC_URL + '/Aaron.jpg'}
                        data-holder-rendered="true"
                        id="avatar"/>
                    </div>
                </div>
                <div className="techs">
                    <div className="row d-flex justify-content-center techs-row">
                        <div className="col col-md-6 text-center">
                        <h4 className="bg-light text-dark">languages</h4>
                        <span className="badge rounded-pill bg-dark"><i className="fab fa-php fa-2x"></i></span>
                        <span className="badge rounded-pill bg-dark"><i className="fab fa-js fa-2x"></i></span>
                        <span className="badge rounded-pill bg-dark"><i className="fab fa-java fa-2x"></i></span>
                        <span className="badge rounded-pill bg-dark"><i className="fab fa-python fa-2x"></i></span>
                        <span className="badge rounded-pill bg-dark"><i className="fab fa-html5 fa-2x"></i></span>
                        </div>
                    </div>
                    <div className="row d-flex justify-content-center techs-row">
                        <div className="col col-md-6 text-center">
                        <h4 className="bg-light text-dark">technologies - frameworks - Libraries</h4>
                        <span className="badge rounded-pill bg-dark"><i className="fab fa-css3-alt fa-2x"></i></span>
                        <span className="badge rounded-pill bg-dark"><i className="fab fa-laravel fa-2x"></i></span>
                        <span className="badge rounded-pill bg-dark"><i className="fab fa-react fa-2x"></i></span>
                        <span className="badge rounded-pill bg-dark"><i className="fab fa-node-js fa-2x"></i></span>
                        {/* <span className="badge rounded-pill bg-dark"><i className="fab fa-angular fa-2x"></i></span> */}
                        <span className="badge rounded-pill bg-dark"><i className="fab fa-github fa-2x"></i></span>
                        <span className="badge rounded-pill bg-dark"><i className="fab fa-git-alt fa-2x"></i></span>
                        <span className="badge rounded-pill bg-dark"><i className="fab fa-docker fa-2x"></i></span>
                        <span className="badge rounded-pill bg-dark">Redux</span>
                        <span className="badge rounded-pill bg-dark">jQuery</span>
                        <span className="badge rounded-pill bg-dark">MySQL</span>
                        <span className="badge rounded-pill bg-dark">Android Studio</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
 
export default Home;